@import './styles.scss';

.container {
    height: 100%;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width:320px) {
        min-width: 320px;
        max-width: 573px;
        padding-left: 20px;
        padding-right: 20px;
    }

        @media screen and (min-width:768px) {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

        @media screen and (min-width:1200px) {
        padding-left: 16px;
        padding-right: 16px;
        max-width: 1280px;
    }
}